.linkLeft {
  text-decoration: none;
  color: white;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 5.5vw;
  padding-left: 6vw;
  padding-right: 3vw;
  padding-top: 0.5vh;
  padding-bottom: 0.5vh;
  border: 1vmin solid #ffffff;
  width: 20vw;
  height: 12vh;
  letter-spacing: 0.05em;
  text-align: right;
}

.linkRight {
  text-decoration: none;
  color: white;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-size: 5.5vw;
  padding-left: 3vw;
  padding-right: 6vw;
  padding-top: 0.5vh;
  padding-bottom: 0.5vh;
  border: 1vmin solid #ffffff;
  width: 20vw;
  letter-spacing: 0.05em;
  text-align: left;
  height: 12vh;
}

.flipTitile {
  font-size: 6vw;
  color: transparent;
  padding-left: 5.5vw;
  padding-right: 3vw;
  padding-top: 0.5vh;
  padding-bottom: 0.5vh;
  border: 1vmin solid transparent;
  width: 20vw;
  letter-spacing: 0.05em;
  text-align: right;
  height: 12vh;
}

.wrap {
  width: 100vw;
  height: 100vh;
  background: #141414;
}
.background {
  position: absolute;
  width: 90vw;
  height: 90vh;
  left: 5vw;
  top: 5vh;
  background-image: url(../../assets/images/bg.jpg);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.wrapLeft {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  height: 50vh;
}

